.App {
  text-align: center;
}

.toolbar-button {
  font-size: calc(10px + 2vmin);
  border-radius: 10px;
}

.toolbar-button:hover {
  background-color: #cccccc;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}
